.contact-us {
  &__info {
    margin: 0 0 24px;
  }

  &--last {
    margin-bottom: 32px;
  }

  &__info-title {
    margin: 0 0 8px;

    font-size: 14px;
    line-height: 27px;
    color: $utilityTextColor;
  }

  &__info-link {
    color: inherit;
    text-decoration: none;
    transition: color $transitionDuration;

    &:hover {
      color: $colorBlue;
    }
  }

  &__field {
    margin: 0 0 16px;
  }
}
